.popup {
  display: flex;
  align-items: center;
  justify-content: center;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.6s, opacity 0.6s linear;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
}
.popup__card {
  color: #ffffff;
  position: relative;
}

.popup__image {
  max-width: 75vw;
  max-height: 75vh;
}

.popup__content {
  background-color: #ffffff;
  color: #000000;
  max-width: 430px;
  padding: 34px 36px 36px;
  position: relative;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
}

.popup__header {
  margin: 0;
  font-weight: 900;
  font-size: 24px;
  line-height: 29px;
}

.popup_opened {
  min-height: 100vh;
  visibility: visible;
  opacity: 1;
}
.popup__close {
  border: none;
  position: absolute;
  background: url("../images/close_icon.svg") no-repeat center;
  width: 40px;
  height: 40px;
  right: -40px;
  top: -40px;
  transition: 0.3s;
  cursor: pointer;
}

.popup__close:hover {
  opacity: 0.6;
}

@media screen and (max-width: 767px) {
  .popup__header {
    font-size: 18px;
    line-height: 22px;
  }
  .popup__content {
    margin: 0 19px;
    padding: 25px 22px;
    max-width: 282px;
  }
  .popup__close {
    width: 26px;
    height: 26px;
    right: 7px;
    top: -49px;
  }
}
