.footer {
  max-width: 880px;
  margin: 67px auto 60px;
}

.footer__copyright {
  font-size: 18px;
  line-height: 22px;
  margin: 0;
  color: #545454;
}

@media screen and (max-width: 1279px) {
  .footer {
    margin: 50px auto 47px;
  }
}
@media screen and (max-width: 767px) {
  .footer {
    max-width: 282px;
    margin: 48px auto 36px;
  }
  .footer__copyright {
    font-size: 14px;
    line-height: 17px;
  }
}
